@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
	display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
}

.App{height:100vh}body{background-color:#000;cursor:default}*{transition:all 1s ease}.fade-enter{opacity:0;z-index:1}.fade-enter.fade-enter-active{opacity:1;transition:opacity 250ms ease-in}.fade-enter{opacity:0.01}.fade-enter.fade-enter-active{opacity:1;transition:opacity 500ms ease-in}.fade-exit{opacity:1}.fade-exit.fade-exit-active{opacity:0.01;transition:opacity 300ms ease-in}

.nav-elements{margin:auto;padding:0 10px;color:#fff;text-decoration:none}.nav-elements:hover{color:rgba(225,6,0,0.7)}.active{color:rgba(225,6,0,0.7)}

.Nav{background-color:transparent;justify-content:center;padding:0 10%;padding-top:24px;font-family:'Lato', sans-serif}

#intro{display:flex;height:100vh;flex-basis:auto}@media (max-width: 830px) and (min-width: 320px){#intro{margin-top:70px;flex-wrap:wrap-reverse;height:auto}}

#introText{display:flex;width:100%;flex-direction:column;margin:auto 0;margin-left:10%;font-family:PT Sans;font-size:4rem;color:#fff}#introText .typewriter{display:flex}#introText .typewriter .designation{margin-left:20px;min-width:340px;font-family:Roboto Slab;color:rgba(225,6,0,0.7)}@media (max-width: 520px){#introText{font-size:3rem}}.name{padding:0 10px;background-color:#fff;border-radius:20px;font-family:Merriweather;color:#000;transition:all 0.2s ease-in}.name:hover{color:#fff;background-color:rgba(225,6,0,0.7)}

#photo{display:flex;align-items:center;width:100%;margin:0 0;padding:0 0;overflow-y:hidden;overflow-x:hidden}#photo img{margin:auto}@media screen and (min-width: 1000px){#photo img{height:100vh}}@media screen and (max-width: 830px){#photo img{border-radius:500px}}

#about{display:flex;height:100vh;flex-basis:auto;padding-top:30px}@media (max-width: 830px) and (min-width: 320px){#about{margin-top:70px;height:auto}#about .image-container{display:none}#about .content-wrapper #content{margin:auto 20px !important}}

.content-wrapper{display:flex;justify-content:center;align-items:center}#content{color:#fff;background-color:rgba(225,6,0,0.7);margin:0 10% 0 30%;padding:20px;text-align:justify;border-radius:50px 0px 50px 0px}

.image-container{display:flex;width:50%}

